import React from "react";

function BarreVerticale() {
  const style = {
    width: "10px",
    height: "40px",
    backgroundColor: "#FFB900",
    marginTop: "18px",
    marginRight: "20px"
  };
  return <span style={style}></span>;
}

export default BarreVerticale;



